.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 15px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

body {
  background-image: url(/assets/images/16BitRiver.jpg);
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
/* End of base react stuff  */

/* start of navbar css */
.focus {
  text-align: center;
  color: black;
  background-color: #f2f4f3;
  border-radius: 25px;
}
#navName {
  background-color: #22333b;
  margin-bottom: 0;
  color: #f2f4f3;
}

#mainNav {
  margin-top: 0;
  background-color: #22333b;
  margin-bottom: 30px;
  color: #f2f4f3;
  text-align: center;
}

#focusedProject {
  text-align: center;
  color: white;
  background-color: black;
}

.navPill {
  text-align: center;
}

.navPill:hover {
  background-color: #0d3b66;
  -webkit-animation: grow 0.25s linear;
          animation: grow 0.25s linear;
  -webkit-transform: scale(1.12);
      -ms-transform: scale(1.12);
          transform: scale(1.12);
  border-radius: 25px;
  z-index: 3;
}

.fa-arrow-left {
  color: white;
  -webkit-animation: pulse infinite 1s linear;
          animation: pulse infinite 1s linear;
}

.fa-arrow-left:hover {
  -webkit-animation: pulseGrow 0.25s linear;
          animation: pulseGrow 0.25s linear;
  -webkit-transform: scale(1.12);
      -ms-transform: scale(1.12);
          transform: scale(1.12);
  color: red;
}

.fa-star {
  -webkit-animation: wiggle infinite 2s linear;
          animation: wiggle infinite 2s linear;
}

.sticky {
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
}

.sticky + #content {
  padding-top: 70px;
}

@media (max-width: 415px) {
  .sticky + #content {
    padding-top: 155px;
  }
  #focusedProject {
    background-color: black;
    color: #f2f4f3;
    position: fixed;
    margin-bottom: 0;
    bottom: 0;
    z-index: 1;
    width: 100%;
    text-align: left;
  }
}
@media (max-width: 767px) {
  .sticky + #content {
    padding-top: 155px;
  }
}
/* end of navbar css */

/* start of RenderSingle css */
.singleImage {
  margin-bottom: 5px;
  margin-right: 5px;
  border-radius: 25px;
}

.description {
  text-align: left;
  font-size: 18px;
}

@media (min-width: 415px) {
  .singleImage {
    float: left;
    max-width: 30%;
  }
}

.resultsHeader {
  font-size: 20px;
  color: gray;
  margin-bottom: 5px;
  clear: left;
}

.apiResults {
  margin-bottom: 3px;
}

.singleLink {
  font-size: 30px;
  text-align: center;
}

.singleTextBox {
  text-align: left;
  /* background-image: url(/assets/images/stardust.png); */
  background-color: #22333b;
  color: #f2f4f3;
  margin-bottom: 30px;
  padding: 10px;
  border-radius: 25px;
}

.techUsed {
  text-align: center;
}
/* end of renderSingle css */

/* thumbnails */
.projectThumbnail,
.featuredThumbnail {
  background-color: #22333b;
  background-image: url("https://www.transparenttextures.com/patterns/3px-tile.png");
  color: white;
  padding: 0px;
  margin-bottom: 15px;
}

.featuredThumbnail :hover {
  background-color: #22333b;
  background-image: url("https://www.transparenttextures.com/patterns/45-degree-fabric-light.png");
  color: #22333b;
}

.featuredImg :hover {
  -webkit-animation: grow 0.25s linear;
          animation: grow 0.25s linear;
  -webkit-transform: scale(1.12);
      -ms-transform: scale(1.12);
          transform: scale(1.12);
}

#content {
  /* max-width: 50%; */
  /* interesting interaction */
}

/* footer */
#footertext {
  float: none;
  color: white;
  display: block;
  text-align: center;
  font-size: 16px;
}

footer {
  background-color: rgba(0, 0, 0, 0.8) !important;
  margin: 0;
  height: 25px;
}

@media (max-width: 415px) {
  footer {
    display: none;
  }
}

/* animations */
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
  }
  50% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
  75% {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
  }
  50% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
  75% {
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
}

@keyframes grow {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
}

@-webkit-keyframes pulseGrow {
  100% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
}

@keyframes pulseGrow {
  100% {
    -webkit-transform: scale(1.12);
            transform: scale(1.12);
  }
}

@-webkit-keyframes wiggle {
  0% {
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
  25% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
  }
  75% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
}

@keyframes wiggle {
  0% {
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
  25% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
  }
  75% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
}

/* quality of life pointers */
.navPill,
.featuredThumbnail,
.fa-arrow-left,
#newArrow,
.featuredImg:hover {
  cursor: pointer;
}



